'use client';

import { useState, useEffect } from 'react';
import type { JournalEntry } from '@/types';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import Link from 'next/link';
import { format, parseISO } from 'date-fns';
import SentimentDisplay from '@/components/reflectify/SentimentDisplay';
import { ArrowLeft, BookOpen, Loader2, Lightbulb, MessageSquareHeart, Info, Target } from 'lucide-react';

const JOURNAL_HISTORY_KEY = 'reflectifyJournalHistory';

export default function HistoryPage() {
  const [journalHistory, setJournalHistory] = useState<JournalEntry[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    try {
      const storedHistory = localStorage.getItem(JOURNAL_HISTORY_KEY);
      if (storedHistory) {
        const parsedHistory: JournalEntry[] = JSON.parse(storedHistory);
        parsedHistory.sort((a, b) => parseISO(b.date).getTime() - parseISO(a.date).getTime());
        setJournalHistory(parsedHistory);
      }
    } catch (error) {
      console.error("Failed to load journal history from localStorage:", error);
      // Consider adding a toast notification for the user about the error
    }
    setIsLoading(false);
  }, []);

  const getCategoryDescription = (category?: string) => {
    if (!category) return null;
    switch (category) {
      case 'POSSIBLE_OUTCOME':
        return 'Potential external event/outcome';
      case 'PAST_TRAIT':
        return 'Personal trait/past behavior';
      case 'ABSOLUTE_TRUTH':
        return 'Perceived unchangeable fact/limiting belief';
      default:
        return 'General';
    }
  };


  if (isLoading) {
    return (
      <div className="container mx-auto max-w-3xl py-8 px-4 flex flex-col justify-center items-center min-h-[calc(100vh-150px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary mb-4" />
        <p className="text-lg text-muted-foreground">Loading history...</p>
      </div>
    );
  }

  return (
    <div className="container mx-auto max-w-3xl py-8 px-4">
      <div className="mb-8 flex items-center justify-between">
        <h1 className="text-3xl font-bold text-foreground">Journal History</h1>
        <Button asChild variant="outline" size="sm">
          <Link href="/" className="flex items-center">
            <ArrowLeft className="mr-2 h-4 w-4" />
            Back to Journal
          </Link>
        </Button>
      </div>

      {journalHistory.length === 0 ? (
        <Card className="shadow-lg rounded-xl text-center py-10">
          <CardHeader className="pb-2">
            <BookOpen className="mx-auto h-16 w-16 text-muted-foreground mb-4" />
            <CardTitle className="text-xl">No Entries Yet</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground mb-6">
              You haven&apos;t written any journal entries. Start reflecting on the main page!
            </p>
            <Button asChild>
              <Link href="/">Start Journaling</Link>
            </Button>
          </CardContent>
        </Card>
      ) : (
        <div className="space-y-6">
          {journalHistory.map((entry) => (
            <Card key={entry.id} className="shadow-md rounded-lg overflow-hidden">
              <CardHeader className="bg-card">
                <div className="flex justify-between items-start">
                    <div>
                        <CardTitle className="text-lg font-semibold text-foreground">
                        {format(parseISO(entry.date), 'EEEE, MMMM dd, yyyy')}
                        </CardTitle>
                        <CardDescription className="text-sm text-muted-foreground">
                        {format(parseISO(entry.date), 'p')}
                        </CardDescription>
                    </div>
                </div>
              </CardHeader>
              <CardContent className="pt-4 space-y-4">
                {entry.goalText && (
                  <div className="p-3 bg-primary/10 rounded-md border border-primary/30">
                    <h4 className="font-semibold text-primary-foreground mb-1 text-md">My Goal:</h4>
                    <p className="text-sm text-foreground whitespace-pre-wrap">{entry.goalText}</p>
                    <div className="mt-2 text-xs text-muted-foreground">
                      <span>Achievability: {entry.goalAchievability ?? 'N/A'}/10</span>
                      {entry.goalDeadline && (
                        <span className="ml-4">Deadline: {format(parseISO(entry.goalDeadline), 'PPP')}</span>
                      )}
                    </div>
                  </div>
                )}

                {entry.failureReason && (
                  <div className="p-3 bg-destructive/10 rounded-md border border-destructive/30 mt-4">
                    <h4 className="font-semibold text-destructive mb-1 text-md">Potential Failure Analysis:</h4>
                    <p className="text-sm text-foreground whitespace-pre-wrap"><span className="font-medium">Original Reason:</span> {entry.failureReason}</p>
                    {entry.failureCategory && entry.categorizedFailureStatement && (
                       <div className="mt-2 p-2 bg-muted/20 rounded-md border">
                         <p className="text-xs text-foreground"><Info className="inline-block mr-1 h-3 w-3 text-primary"/> <span className="font-semibold">AI Categorization ({getCategoryDescription(entry.failureCategory)}):</span> {entry.categorizedFailureStatement}</p>
                       </div>
                    )}
                    {entry.leftHandText && (
                      <div className="mt-2 p-2 bg-muted/20 rounded-md border">
                        <p className="text-xs text-foreground"><span className="font-semibold">Left Hand:</span> {entry.leftHandText}</p>
                      </div>
                    )}
                    {entry.rightHandText && (
                      <div className="mt-1 p-2 bg-muted/20 rounded-md border">
                        <p className="text-xs text-foreground"><span className="font-semibold">Right Hand:</span> {entry.rightHandText}</p>
                      </div>
                    )}
                  </div>
                )}
                
                {entry.insightsText && (
                  <div className="p-3 bg-accent/10 rounded-md border border-accent/30 mt-4">
                     <h4 className="font-semibold text-accent-foreground mb-1 text-md flex items-center">
                        <MessageSquareHeart className="mr-2 h-5 w-5" /> Insights and Realizations:
                    </h4>
                    <p className="text-sm text-foreground whitespace-pre-wrap">{entry.insightsText}</p>
                  </div>
                )}

                {entry.goalImprovementText && (
                  <div className="p-3 bg-primary/10 rounded-md border border-primary/30 mt-4">
                    <h4 className="font-semibold text-primary-foreground mb-1 text-md flex items-center">
                        <Target className="mr-2 h-5 w-5" /> Goal Improvement Ideas:
                    </h4>
                    <p className="text-sm text-foreground whitespace-pre-wrap">{entry.goalImprovementText}</p>
                  </div>
                )}


                {entry.negativeCharacteristics && entry.negativeCharacteristics.length > 0 && (
                  <div>
                    <h4 className="font-semibold text-foreground mb-2 text-md">Negative Characteristics Rated:</h4>
                    <ul className="space-y-2">
                      {entry.negativeCharacteristics.map((char, index) => (
                        <li key={index} className="text-sm p-3 border rounded-md bg-card flex justify-between items-center">
                          <span>{char.name}</span>
                          <Badge
                            variant={
                              char.rating <= -5 ? 'destructive' :
                              (char.rating >= -4 && char.rating <=4) ? 'secondary' 
                              : 'default' // for char.rating > 4
                            }
                            style={char.rating > 4 ? { backgroundColor: 'hsl(var(--accent))', color: 'hsl(var(--accent-foreground))' } : {}}
                            className="text-xs px-2 py-0.5"
                          >
                            Rating: {char.rating}
                          </Badge>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}

                {entry.entryText && (
                  <div>
                    <h4 className="font-semibold text-foreground mb-2 text-md">General Thoughts:</h4>
                    <p className="text-sm text-foreground/90 whitespace-pre-wrap bg-muted/20 p-3 rounded-md border">
                      {entry.entryText}
                    </p>
                  </div>
                )}
                
                {!entry.entryText && (!entry.negativeCharacteristics || entry.negativeCharacteristics.length === 0) && !entry.goalText && !entry.failureReason && !entry.insightsText && !entry.goalImprovementText && (
                    <p className="text-sm text-muted-foreground italic">No specific details recorded for this entry.</p>
                )}
                 {!entry.entryText && (!entry.negativeCharacteristics || entry.negativeCharacteristics.length === 0) && (entry.goalText || entry.failureReason || entry.insightsText || entry.goalImprovementText) && (
                    <p className="text-sm text-muted-foreground italic">No further general thoughts or characteristics recorded for this entry.</p>
                )}


                {entry.sentimentAnalysis && entry.entryText && (
                  <div>
                     <h4 className="font-semibold text-foreground mb-2 text-md">Sentiment of Thoughts:</h4>
                    <SentimentDisplay
                      sentiment={entry.sentimentAnalysis.sentiment}
                      score={entry.sentimentAnalysis.score}
                    />
                  </div>
                )}
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}

