import type { FC } from 'react';
import { Badge } from '@/components/ui/badge';
import { TrendingUp, TrendingDown, MinusCircle } from 'lucide-react';
import { cn } from '@/lib/utils';

interface SentimentDisplayProps {
  sentiment?: string;
  score?: number;
}

const SentimentDisplay: FC<SentimentDisplayProps> = ({ sentiment, score }) => {
  if (sentiment === undefined || score === undefined) {
    return <p className="text-muted-foreground">Sentiment analysis not available yet.</p>;
  }

  let sentimentIcon;
  let badgeVariant: "default" | "destructive" | "secondary" = "secondary";
  let scoreColorClass = "text-foreground"; 
  let iconColorClass = "text-muted-foreground";

  if (sentiment.toLowerCase() === 'positive') {
    iconColorClass = "text-accent";
    sentimentIcon = <TrendingUp className={cn("mr-2 h-5 w-5", iconColorClass)} />;
    badgeVariant = "default"; 
    scoreColorClass = "text-accent";
  } else if (sentiment.toLowerCase() === 'negative') {
    iconColorClass = "text-destructive";
    sentimentIcon = <TrendingDown className={cn("mr-2 h-5 w-5", iconColorClass)} />;
    badgeVariant = "destructive";
    scoreColorClass = "text-destructive";
  } else { // Neutral
    iconColorClass = "text-muted-foreground"; 
    sentimentIcon = <MinusCircle className={cn("mr-2 h-5 w-5", iconColorClass)} />;
    badgeVariant = "secondary";
    scoreColorClass = "text-foreground"; 
  }
  
  const badgeStyle = sentiment.toLowerCase() === 'positive' 
    ? { backgroundColor: 'hsl(var(--accent))', color: 'hsl(var(--accent-foreground))' } 
    : {};


  return (
    <div className="flex flex-col sm:flex-row sm:items-center gap-4 p-4 bg-card rounded-lg shadow">
      <div className="flex items-center">
        {sentimentIcon}
        <Badge variant={badgeVariant} style={badgeStyle} className="text-sm capitalize px-3 py-1">
          {sentiment}
        </Badge>
      </div>
      <div className="flex items-baseline gap-2">
        <span className="text-sm text-muted-foreground">Sentiment Score:</span>
        <span className={cn("text-lg font-semibold", scoreColorClass)}>
          {score.toFixed(2)}
        </span>
         <span className="text-xs text-muted-foreground"> (from -1 to 1)</span>
      </div>
    </div>
  );
};

export default SentimentDisplay;
